import React, { useState, useRef, useEffect } from "react";
import { Col, Row, Container, Button, Modal, Tabs, Tab } from "react-bootstrap";
import { navigate, Link } from "gatsby";
import { TailSpin } from "react-loader-spinner";

import Layout from "../components/layout.js";
import val from "../components/validadores";
import Seo from "../components/seo";
import Keyboards from "../components/Keyboards.js";
import "../components/pages-css/sCita.css";
import VideoSeccion from "../components/videoSeccion.js";
import ModalEmergente from "../components/ModalEmergente.js";
import Timer from "../components/timer.js";
import api from "../components/actions/riveroapi";

export default function SinCita() {
  
  const [show, setShow] = useState(false);
  const [modal, setModal] = useState(false);
  const [errorInput, setErrorInput] = useState(false);
  const [error, setError] = useState(0);
  const [preferencia, setPreferencia] = useState("WhatsApp");
  const [click, setClick] = useState(false);
  const [urlApi, seturlApi] = useState("phone");
  const [mensajeModal, setMensajeModal] = useState("");
  const [loading, setLoading] = useState(false);

  const refForm = useRef();

  const [optionsKeyboard, setOptionsKeyboard] = useState({
    inpName: "",
    layoName: "phone",
    name: "",
    value: {
      Campo: undefined
    }
  })

  useEffect(() => {
    if (typeof window !== 'undefined') {
        //console.log('we are running on the client');
        setOptionsKeyboard({
          ...optionsKeyboard,
          value: {
            Campo: localStorage.getItem("Campo") || "",
          }
        })
    } else {
        //console.log('we are running on the server');
    }
  }, [])

  function cambioPreferencia(data){
    ////console.log("Estamos en el cambioPreferencia SIN_CITA: ", data)

    if(data === "WhatsApp"){
      setOptionsKeyboard({
        ...optionsKeyboard,
          layoName: "phone"
      })
      seturlApi("phone");
      setPreferencia("WhatsApp");
    } else if (data === "Correo"){
      setOptionsKeyboard({
        ...optionsKeyboard,
          layoName: "default"
      })
      seturlApi("mail");
      setPreferencia("Correo");
    } else if (data === "VIN"){
      setOptionsKeyboard({
        ...optionsKeyboard,
          layoName: "vin"
      })
      seturlApi("vin");
      setPreferencia("VIN");
    }

    document.getElementById("btnWhatsApp").className =
      "btn btn-preferencia"
    document.getElementById("btnCorreo").className =
      "btn btn-preferencia"
    document.getElementById("btnVIN").className =
      "btn btn-preferencia"
    document.getElementById("btn" + data).className =
      "btn btn-preferenciaActivo"

  }

  function continuar(){
    //console.log("Pasa lo siguiente: ");

    const datos = {
      data: optionsKeyboard.value.Campo,
      url: urlApi,
    }

    if(preferencia === "WhatsApp"){
      val.ValidadorWhatsApp(optionsKeyboard.value.Campo).then(r => {
        //console.log("Tengo el repsonse: ", r);
        if(r.data === 5){
          setLoading(true);
          localStorage.setItem('Campo', optionsKeyboard.value.Campo);
          //navigate("/kilometraje")
          api.getSinCita(datos).then((resp) => {
            //console.log("Tengo: ", resp);
            if(resp === 2 || resp === 1){
              //console.log("Incorrecto");
              setModal(true)
              setError(resp);
              setLoading(false);
            } else {
              //console.log("Correcto");
              setLoading(false);
              setModal(true)
              setError(14);
              var newName = resp.datosCliente.fullName.replace(/,/g, " ")
              setMensajeModal(
                <div style={{overflowY: 'scroll', height: 400}}>
                  <center>
                    <div style={{marginBottom: 15}}>{newName}</div>
                    {          
                      resp.CarrosCliente.map((obj, i) => {
                        const params = {
                          data: obj,
                          id: resp.id,
                          codigo: resp.codigo,
                          datosCliente: resp.datosCliente,
                          direccionCliente: resp.direccionCiente,
                          contactoCliente: resp.contactoCliente,
                        }
                        return(
                          <Row className="form-control text-left" style={{margin: 10}} onClick={() => selecCarro(params)}>
                            {obj.model} - {obj.year}, Color: {obj.color}, VIN: {obj.vin}, Placas: {obj.plates}
                          </Row>
                        )
                      })
                    }
                  </center>
                </div>
              )
            }
          })
          .catch((error) => {
            setLoading(false);
            setModal(true)
            setError(4);
          });
        } else {
          setModal(true)
          setError(r.data);
          setErrorInput(true);
          setLoading(false);
        }
      })
    } else if (preferencia === "Correo"){
      val.ValidadorCorreo(optionsKeyboard.value.Campo).then((r) => {
          if(r.data === 5){
            setLoading(true);
            localStorage.setItem('Campo', optionsKeyboard.value.Campo);
            //navigate("/kilometraje")
            api.getSinCita(datos).then((resp) => {
              //.log("Tengo: ", resp);
        
              if(resp === 2 || resp === 1){
                //console.log("Incorrecto");
                setModal(true)
                setError(resp);
                setLoading(false);
              } else {
                //console.log("Correcto");
                setModal(true)
                setError(14);
                setLoading(false);
                var newName = resp.datosCliente.fullName.replace(/,/g, " ")
                setMensajeModal(
                    <center>
                      <div style={{marginBottom: 15}}>{newName}</div>
                      {          
                        resp.CarrosCliente.map((obj, i) => {
                          const params = {
                            data: obj,
                            id: resp.id,
                            codigo: resp.codigo,
                            datosCliente: resp.datosCliente,
                            direccionCliente: resp.direccionCiente,
                            contactoCliente: resp.contactoCliente,
                          }
                          return(
                            <Row className="form-control text-left" style={{margin: 10}} onClick={() => selecCarro(params)}>
                              {obj.model} - {obj.year}, Color: {obj.color}, VIN: {obj.vin}, Placas: {obj.plates}
                            </Row>
                          )
                        })
                      }
                    </center>
                )
              }
            })
            .catch((error) => {
              setModal(true)
              setError(4);
              setLoading(false);
            });
          } else {
            setModal(true)
            setError(r.data);
            setErrorInput(true);
          }
      })
    } else if (preferencia === "VIN"){
      val.ValidadorVIN(optionsKeyboard.value.Campo).then((r) => {
        setLoading(true);
        //console.log("Le dio en continuar en la sección VIN", optionsKeyboard.value.Campo) V561A56V
          api.getSinCita(datos).then((resp) => {
            if(resp === 3){
              //console.log("Incorrecto: ", resp);
              setModal(true)
              setError(resp);
              setErrorInput(true);
              setLoading(false);
            } else {
              //console.log("Correcto: ", resp);
              setModal(true)
              setError(14);
              setLoading(false);
              setMensajeModal(
                <center>
                    <div style={{marginBottom: 15}}>{resp.datosCliente.fullName}</div>
                    <Row className="form-control text-left" style={{margin: 10}} onClick={() => selecCarro(resp)}>
                      {resp.data.model} - {resp.data.year}, Color: {resp.data.color}, VIN: {resp.data.vin}, Placas: {resp.data.plates}
                    </Row>
                </center>
              )
            }
          })
          .catch((error) => {
            setModal(true)
            setError(4);
            setLoading(false);
          });
      })
    }
  }

  function selecCarro(data){  
    //console.log(data)  
    navigate("/kilometraje")
    localStorage.setItem("RutaNavegacion", "sin-cita");
    localStorage.setItem("informacion", JSON.stringify(data));
  }

  return (
    <Layout backUrl={"/"}>
      <Seo title="Auto servicio" />
        {loading === true ? 
            <Container style={{position: 'absolute', zIndex: 100, height: '80%', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                <TailSpin
                  height="100"
                  width="100"
                  color="#0d6efd"
                  ariaLabel="tail-spin-loading"
                  radius="0"
                  wrapperStyle={{}}
                  wrapperClass=""
                  visible={true}
                />
            </Container>
        : null}
        <Container className="container-sin-cita" onClick={() => {setClick(true)}}>
          {<Timer click={click} setClick={setClick} setError={setError} setModal={setModal}/>}
            <Row className="row-cita-arriba">
                <div className="container-cita">
                  <Row>
                    <Col className="titulo-cita"><h1 className="titulo-h1">¿Qué tal?, Muy buen día.</h1></Col>
                  </Row>
                  <Row>
                    <Col className="titulo-cita" style={{flexDirection: 'column'}}>
                      <h4> Buscar mi cuenta</h4>
                      <p className="texto-gris">*¿Cómo quieres buscar?</p>
                    </Col>
                  </Row>
                  <Row style={{display: 'flex', justifyContent: 'center'}}>
                    <Row style={{display: 'flex', justifyContent: 'space-around', width: '70%'}}>
                      <Button className="btn-preferenciaActivo" id="btnWhatsApp" onClick={() => cambioPreferencia("WhatsApp")}>WhatsApp</Button>
                      {/*<Button className="btn-preferencia" id="btnCorreo" onClick={() => cambioPreferencia("Correo")}>Correo</Button>*/}
                      <Button className="btn-preferencia" id="btnVIN" onClick={() => cambioPreferencia("VIN")}>VIN</Button>
                    </Row>  
                  </Row>
                  <br/>
                  <Row className="row-cita-abajo" style={{marginBottom: 0}}>
                      <Col lg={9}>
                          <input
                            autoComplete="off"
                            type="text"
                            placeholder={`Ingrese su ${preferencia}`}
                            id={errorInput === true ? "inputContactoError" : "inputContacto"}
                            className="form-control"
                            onClick={() => setShow(true)}
                            onFocus={() => setOptionsKeyboard({...optionsKeyboard, inpName: "Campo"})}
                            value={optionsKeyboard.value.Campo || ""}
                          />
                    
                      </Col>
                  </Row>
                  <Row>
                    <Col className="titulo-cita">
                      {preferencia === "WhatsApp" ? 
                        <p className="texto-gris" style={{fontSize: 12}}>Ingresa el teléfono que registraste con nosotros en tu compra o última visita.</p>  
                      : null}
                      {preferencia === "Correo" ? 
                        <p className="texto-gris" style={{fontSize: 12}}>Ingresa el correo electrónico que registraste con nosotros en tu compra o última visita.</p>  
                      : null}
                      {preferencia === "VIN" ? 
                        <p className="texto-gris" style={{fontSize: 12}}>El número de serie lo podrás encontrar en tu tarjeta de circulación, en la póliza de garantía o en la placa de identificación del vehículo. Agregar los últimos 8 digitos. <a onClick={() => {setError(12); setModal(true)}}>Ver imagen de ejemplo</a></p>  
                      : null}
                    </Col>
                  </Row>
                  <Button ref={refForm} onClick={() => continuar()} style={{display: 'none'}}></Button>
                  {show === true ? 
                      <center className="pt-4">
                        <Keyboards options={optionsKeyboard} setOptionsKeyboard={setOptionsKeyboard} optionsKeyboard={optionsKeyboard}/>
                      </center>
                  : null }
                </div>
            </Row>
            <Row className="row-cita-abajo">
              <VideoSeccion refForm={refForm} none="tiene"/>
            </Row>
        </Container>
        <ModalEmergente modal={modal} error={error} setModal={setModal} registro={true} setClick={setClick} mensajeModal={mensajeModal}/>
    </Layout>
  )
}
